.TodoInput{
  border: none;
  outline: none;
  border-bottom: 1px solid #ededed;
  font-size: 1.6em;
  color: #666;
  line-height: 1.8em;
  width: 20em;
  padding: 5px;
  font-weight: 200;
  -webkit-box-shadow: inset 0 -2px 1px rgba(0,0,0,0.03);
          box-shadow: inset 0 -2px 1px rgba(0,0,0,0.03);
  -webkit-box-shadow: 0px 0px 20px 2px rgba(0,0,0,.1);
          box-shadow: 0px 0px 20px 2px rgba(0,0,0,.1);
}
.TodoInput::-webkit-input-placeholder {
  color: #d9d9d9;
  font-style: italic;
}


